.tiles{margin-left:25px}.tiles .tile{position:relative;width:40px;height:55px;overflow:hidden;float:left}.tiles .tile .char-holder{position:absolute}.tiles .tile .tile-char{width:45px;height:55px;font-size:45px;opacity:.5;transition:all 2s ease;font-family:monospace;font-weight:700}.tiles .tile .tile-char.active{opacity:1;text-shadow:0 4px 3px rgba(0,0,0,.4),0 8px 8px rgba(0,0,0,.1),0 12px 4px rgba(0,0,0,.1)}